import React, {useCallback, useEffect, useMemo, useState} from 'react';
import {useHistory, useLocation} from 'react-router-dom';
import {useSelector} from 'react-redux';

import {
	AppBarButtons,
	SummaryList,
	DescriptionPageContainer,
	SubHeader,
	SubHeaderText,
} from '../../../styles/components/style';
import IAM_USER_GROUP from '../../../reducers/api/IAM/User/Group/group';
import GroupSummary from '../Components/GroupSummary';
import {arrowDownIcon, arrowRightIcon} from '../../../icons/icons';
import {IconButton} from '../../../styles/components/icons';
import {
	CurrentPathContainer,
	NextPath,
	PathLink,
} from '../../../styles/components/currentPath';
import {
	NormalButton,
	TransparentButton,
} from '../../../styles/components/buttons';
import {FOLD_DATA} from '../../../utils/data';
import {LiText} from '../../../styles/components/text';
import IAM_USER_GROUP_TYPE from '../../../reducers/api/IAM/User/Group/groupType';
import TabBar from '../../Tab/TabBar';
import qs from 'qs';
import GroupRolesTab from '../Components/GroupRolesTab';
import GroupUsersTab from '../Components/GroupUsersTab';
import GroupOnDescPageTags from '../Components/GroupOnDescPageTags';
import PropTypes from 'prop-types';
import {TabContainer} from '../../../styles/components/tab';

const GroupDescriptionSpace = ({groupId}) => {
	const history = useHistory();
	const {search} = useLocation();

	const {groups} = useSelector(IAM_USER_GROUP.selector);
	const {groupTypes} = useSelector(IAM_USER_GROUP_TYPE.selector);

	const [isSummaryOpened, setIsSummaryOpened] = useState(true);
	const [isOpend, setIsOpend] = useState(true);
	const [isTableFold, setIsTableFold] = useState(FOLD_DATA);

	const TabBarInfo = [
		{name: '사용자', href: 'user'},
		{name: '권한', href: 'role'},
		{name: '태그', href: 'tag'},
	];

	const group = useMemo(() => groups.find((v) => v.id === groupId), [
		groups,
		groupId,
	]);

	const onClickFoldSummary = useCallback(() => {
		setIsSummaryOpened(!isSummaryOpened);
	}, [isSummaryOpened]);

	const onClickChangeGroupName = useCallback(() => {
		setIsOpend(true);
	}, [setIsOpend]);
	// if groupId does not exist, direct to 404 page
	useEffect(() => {
		if (groupId && !group) {
			history.push('/404');
		}
	}, [groupId, group, history]);

	return (
		<DescriptionPageContainer>
			<CurrentPathContainer>
				<PathLink to='/iam'>IAM</PathLink>
				<NextPath>{' > '}</NextPath>
				<PathLink to='/groups'>사용자 그룹</PathLink>
				<NextPath>{' > '}</NextPath>
				<PathLink to={`/groups/${groupId}`}>{group?.name}</PathLink>
			</CurrentPathContainer>

			<SubHeader>
				<SubHeaderText>
					<IconButton
						color={'font'}
						size={'m'}
						margin={'0px'}
						onClick={onClickFoldSummary}
					>
						{isSummaryOpened ? arrowDownIcon : arrowRightIcon}
					</IconButton>
					요약 [ {group?.name} ]
				</SubHeaderText>
				<AppBarButtons>
					<NormalButton onClick={onClickChangeGroupName}>
						그룹명 편집
					</NormalButton>
					<TransparentButton margin={'0px 0px 0px 5px'}>
						삭제
					</TransparentButton>
				</AppBarButtons>
			</SubHeader>

			<SummaryList>
				<LiText>그룹명 : {group?.name}</LiText>
				<LiText>
					그룹 유형 :{' '}
					{
						groupTypes.find((v) => v.id === group.clientGroupTypeId)
							.name
					}
				</LiText>
				<LiText>생성 일시 : {group?.creationDate}</LiText>
			</SummaryList>

			{isSummaryOpened && (
				<GroupSummary
					groupId={groupId}
					isOpened={isSummaryOpened}
					setIsOpened={setIsSummaryOpened}
				/>
			)}

			<div>
				<TabContainer
					className={isSummaryOpened ? 'tabBar fix' : 'tabBar'}
				>
					<TabBar
						Tabs={TabBarInfo}
						param={'groups'}
						Id={groupId}
						isOpened={isSummaryOpened}
						setIsOpened={setIsSummaryOpened}
					/>
				</TabContainer>
				{!isSummaryOpened && (
					<div style={{padding: '10px 16px'}}>
						{qs.parse(search, {ignoreQueryPrefix: true}).tabs ===
							'role' && (
							<GroupRolesTab
								groupId={groupId}
								space={'GroupRolesTab'}
								isFold={isTableFold}
								setIsFold={setIsTableFold}
							/>
						)}
						{qs.parse(search, {ignoreQueryPrefix: true}).tabs ===
							'user' && (
							<GroupUsersTab
								groupId={groupId}
								space={'GroupUsersTab'}
								isFold={isTableFold}
								setIsFold={setIsTableFold}
							/>
						)}
						{qs.parse(search, {ignoreQueryPrefix: true}).tabs ===
							'tag' && (
							<GroupOnDescPageTags
								groupId={groupId}
								space={'GroupOnDescPageTags'}
								isFold={isTableFold}
								setIsFold={setIsTableFold}
							/>
						)}
					</div>
				)}
			</div>
		</DescriptionPageContainer>
	);
};

GroupDescriptionSpace.propTypes = {
	groupId: PropTypes.string.isRequired,
};

export default GroupDescriptionSpace;
